<template>
  <div class="sql-view">
    <div class="operate-region">
      <!-- <el-radio-group v-model="codeType" size="mini">
        <el-radio-button label="原始代码"></el-radio-button>
        <el-radio-button label="格式化代码"></el-radio-button>
      </el-radio-group> -->
      <el-checkbox-group v-model="codeType" size="mini">
        <el-checkbox-button v-for="item in codeTypeOpts" :label="item" :key="item">{{ item }}</el-checkbox-button>
      </el-checkbox-group>
      <!-- <el-button-group>
        <el-button type="primary" size="mini" @click="onCopyOriginCode">原始代码</el-button>
        <el-button type="primary" size="mini" @click="onCopyFormatCode">格式化代码</el-button>
      </el-button-group> -->
    </div>
    <MonacoEditor class="editor" v-model="originCode" language="sql" theme="vs-dark" @contentChange="onContentChange" />
    <MonacoEditor class="editor" v-model="formatCode" language="sql" theme="vs-dark" />
  </div>
</template>

<script>
import MonacoEditor from "../components/MonacoEditor.vue";
import { format } from "sql-formatter";

const CodeTypes = ["原始代码", "格式化代码"];

export default {
  components: {
    MonacoEditor,
  },
  data() {
    return {
      codeTypeOpts: CodeTypes,
      codeType: CodeTypes,
      originCode: "",
      formatCode: "",
    };
  },
  mounted() {
    this.originCode = window.sessionStorage.getItem("origin-code");
  },
  methods: {
    onContentChange(content) {
      window.sessionStorage.setItem("origin-code", content);
      this.formatCode = this.sqlFormat(content);
    },
    sqlFormat(sql) {
      const formattedSQL = format(sql, { language: "mysql" });
      return formattedSQL;
      // return sql.replace(/[\r\n]/g, " ");
    },
    //#region - Action
    onCopyOriginCode() {
      navigator.clipboard
        .writeText(this.originCode)
        .then(() => {
          this.$message.success("原始代码已复制");
        })
        .catch((err) => {
          this.$message.error("复制发生异常");
        });
    },
    onCopyFormatCode() {
      // 使用 Clipboard API 将文本复制到剪贴板
      navigator.clipboard
        .writeText(this.formatCode)
        .then(() => {
          this.$message.success("格式化代码已复制");
        })
        .catch((err) => {
          this.$message.error("复制发生异常");
        });
    },
    //#endregion
  },
};
</script>

<style scoped>
.sql-view {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.operate-region {
  padding: 8px;
}

.editor {
  width: 100%;
  flex: 1;
}
</style>



