<template>
  <div class="main">
    <div class="side-bar">
      <div class="logo">DevTool</div>
      <el-menu :default-active="menuIndex" @select="handleSelect" background-color="#2d3a4b" text-color="#fff" active-text-color="#38ccff">
        <el-menu-item index="1">SQL</el-menu-item>
        <el-menu-item index="2">HelloWorld</el-menu-item>
        <el-menu-item index="3">About</el-menu-item>
      </el-menu>
    </div>

    <div class="container">
      <div class="header">this is header</div>
      <div class="content">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
const RouteNames = { 1: "SQLView", 2: "HelloWorld", 3: "about" };

export default {
  name: "Main",
  data() {
    return { menuIndex: "1" };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      const name = RouteNames[key];
      this.$router.push({ name });
    },
  },
};
</script>

<style scoped>
.main {
  height: 100%;
  display: flex;
  flex-direction: row;
}

.side-bar {
  width: 200px;
  overflow: hidden;
}

.container {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.logo {
  height: 56px;
  display: flex;
  align-items: center;
  padding-left: 20px;
  color: #409eff;
  font-weight: 900;
  background: #283445;
}

.el-menu {
  min-height: 100%;
  min-width: 100%;
}

.header {
  height: 56px;
  display: flex;
  align-items: center;
}

.content {
  flex: 1;
  padding-right: 20px;
  overflow: scroll;
}
</style>



